import { loadTexture, TextureRegion } from 'gdxjs';
import { Vector2 } from 'gdxjs';

const checkKerning = (id1, id2, kerningsData) => {
  let amount = 0;

  for (let kerning of kerningsData) {
    if (kerning.first === id1 && kerning.second === id2) {
      amount = kerning.amount;
      break;
    }
  }

  return amount;
};

class TextRenderer {
  constructor(pageImage, regions, keringsData, displayWidth) {
    this.pageImage = pageImage;
    this.regions = regions;
    this.kerningsData = keringsData;
    this.align = 2;
    this.lineHeight = pageImage.lineHeight;
    this.baseLine = pageImage.base;
    this.fixedWidth = displayWidth;
    this.position = new Vector2(0, 0);
    this.scale = 0;
    this.newBase = 0;
    this.oriWidth = 0;
    this.oriHeight = 0;
    this.positionBreak = []; // list of break-position's index

    this.lastBreakIndex = 0; // pre draw

    this.cache = [];
    this.preDraw = true; // pre char sequence

    this.preSequence = '';
    this.x = 0;
    this.y = 0;
    this.rgba = [1, 1, 1, 1];
  }

  setColor(r, g, b, a) {
    this.rgba[0] = r;
    this.rgba[1] = g;
    this.rgba[2] = b;
    this.rgba[3] = a;
  }

  addToCache(line, px, py, lineBreak, lineH) {
    let xAdvance = 0;

    for (let index = 0; index < line.length; index++) {
      const currChar = line.charCodeAt(index);
      const preChar = line.charCodeAt(index - 1);
      let key = this.findRegions(currChar);
      let kerningAmount = checkKerning(preChar, currChar, this.kerningsData);
      this.cache.push({
        key: key,
        x: px + xAdvance + key.xoffset * this.scale + kerningAmount * this.scale,
        y: py + key.yoffset * this.scale + lineBreak * this.scale + lineH,
        width: key.width * this.scale,
        height: key.height * this.scale
      });
      xAdvance += key.xadvance * this.scale;
    }
  }

  beforeDrawing(posX, posY, fontSize, CharSequence) {
    if (this.preDraw) {
      let spaceXadvance = 0;
      let tmpLength = 0;
      let tmplist = []; // find longest sequence

      for (let i = 0; i < CharSequence.length; i++) {
        const char = CharSequence.charCodeAt(i);
        const key = this.findRegions(char);

        if (char !== 32) {
          tmpLength += key.xadvance * this.scale;
        } else {
          spaceXadvance = key.xadvance * this.scale;
          tmplist.push(tmpLength);
          tmpLength = 0;
        }
      }

      let maxSeq = Math.max(...tmplist) + spaceXadvance; //////////////////

      this.newbase = fontSize;
      this.scale = this.newbase / this.baseLine;
      CharSequence = CharSequence.trim(); // split string when meet "/n" char

      let listCharSeq = CharSequence.split('/n').map(s => s.trim());
      let lineH = 0;

      for (let si = 0; si < listCharSeq.length; si++) {
        CharSequence = listCharSeq[si]; // find new origin Width for each line

        this.oriWidth = 0;

        for (let i = 0; i < CharSequence.length; i++) {
          const char = CharSequence.charCodeAt(i);
          const preChar = CharSequence.charCodeAt(i - 1);
          const key = this.findRegions(char);
          let kerningAmount = checkKerning(preChar, char, this.kerningsData);
          this.oriWidth += kerningAmount * this.scale;
          this.oriWidth += key.xadvance * this.scale;
        }

        let tmpRatio = this.oriWidth / this.fixedWidth;
        lineH = this.lineHeight * this.scale * si;
        let accumulate = 0;
        let tmpChar = '';
        let lineBreak = 0; // finding position break and length of each line
        // only execute if oriWidth > fixedWidth and fixedWidth > longest seq

        if (tmpRatio > 1 && this.fixedWidth > maxSeq) {
          for (let i = 0; i < CharSequence.length; i++) {
            tmpChar += CharSequence[i];
            const char = CharSequence.charCodeAt(i);
            const key = this.findRegions(char);
            accumulate += key.xadvance * this.scale; // every line before last line

            if (accumulate >= this.fixedWidth) {
              let j = i;

              while (j >= 0) {
                i--;
                j--;
                const key2 = this.findRegions(CharSequence.charCodeAt(i));
                accumulate -= key2.xadvance * this.scale;
                tmpChar = tmpChar.substring(0, tmpChar.length - 1);

                if (accumulate <= this.fixedWidth && key2.id === 32) {
                  lineBreak = this.lineHeight * this.positionBreak.length;
                  this.positionBreak.push(i); ///

                  this.lastBreakIndex = i;
                  const line = tmpChar.trim();
                  let tmp = 0;
                  let key3 = 0;

                  for (let index = 0; index < line.length; index++) {
                    key3 = this.findRegions(line.charCodeAt(index));

                    if (index === line.length - 1) {
                      tmp += key3.width * this.scale;
                    } else {
                      tmp += key3.xadvance * this.scale;
                    }
                  }

                  let [px, py] = this.getPositionBasedOnMode(posX, posY, tmp);
                  this.addToCache(line, px, py, lineBreak, lineH);
                  accumulate = 0;
                  j = -1;
                  tmpChar = '';
                }
              }
            } // last line


            if (i === CharSequence.length - 1 && accumulate < this.oriWidth) {
              let tmpChar = '';

              for (let j = this.lastBreakIndex; j <= i; j++) {
                tmpChar += CharSequence[j];
              }

              const lastLine = tmpChar.trim();
              let tmpXadvance = 0;
              let lastKey = 0;

              for (let index = 0; index < lastLine.length; index++) {
                lastKey = this.findRegions(lastLine.charCodeAt(index));

                if (index === lastLine.length - 1) {
                  tmpXadvance += lastKey.width * this.scale;
                } else {
                  tmpXadvance += lastKey.xadvance * this.scale;
                }
              }

              let [px, py] = this.getPositionBasedOnMode(posX, posY, tmpXadvance);
              lineBreak = this.lineHeight * this.positionBreak.length;
              this.addToCache(lastLine, px, py, lineBreak, lineH);
            }
          }
        } // if originWidth < fixedWidth or fixedWidth < longest sequence


        if (tmpRatio <= 1 || this.fixedWidth < maxSeq) {
          let [px, py] = this.getPositionBasedOnMode(posX, posY, this.oriWidth);
          this.addToCache(CharSequence, px, py, 0, lineH);
        }
      }

      this.preDraw = false;
      this.oriHeight = this.newbase;
    }
  }

  getPositionBasedOnMode(posX, posY, oriWidth) {
    if (this.align === 0) {
      // posY -= Math.round(this.oriHeight / 2);
      posX = posX + this.fixedWidth / 2 - oriWidth / 2;
    } else if (this.align === 1) {
      posX = posX + this.fixedWidth - oriWidth;
    } else {
      posX = posX;
    }

    return [posX, posY];
  }

  getTextWidth() {
    // goi tu ngoai
    return this.oriWidth;
  }

  setAlignMode(mode) {
    // goi tu ngoai
    this.align = mode;
  }

  setLineHeight(lineHeight) {
    // goi tu ngoai (config line distance)
    this.lineHeight = lineHeight;
  }

  setDisplayWidth(fixedW) {
    this.fixedWidth = fixedW; // this.fixedHeight = fixedH;
  }

  findRegions(id) {
    // em thay khong can thiet
    for (let i = 0; i < this.regions.length; i++) {
      if (this.regions[i].id === id) {
        return this.regions[i];
      }
    }
  }

  draw(batch, charSequence, posX, posY, fontSize = this.baseLine) {
    if (charSequence !== this.preSequence || posX !== this.x || posY !== this.y) {
      this.preDraw = true;
      this.cache.length = 0;
      this.positionBreak.length = 0;
      this.beforeDrawing(posX, posY, fontSize, charSequence);
    }

    const prevColor = batch.color;
    batch.setColor(...this.rgba);

    for (let i = 0; i < this.cache.length; i++) {
      let cache = this.cache[i];
      cache.key.draw(batch, cache.x, cache.y, cache.width, cache.height);
    }

    batch.color = prevColor;
    this.preSequence = charSequence;
    this.x = posX;
    this.y = posY;
  }

}

export const AlignMode = {
  center: 0,
  right: 1,
  left: 2
};

function concatAndResolveUrl(url, concat) {
  let url1 = url.split('/');
  let url2 = concat.split('/');
  let url3 = [];

  for (let i = 0, l = url1.length; i < l; i++) {
    if (url1[i] === '..') {
      url3.pop();
    } else if (url1[i] === '.') {
      continue;
    } else {
      url3.push(url1[i]);
    }
  }

  for (let i = 0, l = url2.length; i < l; i++) {
    if (url2[i] === '..') {
      url3.pop();
    } else if (url2[i] === '.') {
      continue;
    } else {
      url3.push(url2[i]);
    }
  }

  return url3.join('/');
}

export default (async (gl, packFileUrl, textureOptions) => {
  const packFileContent = await fetch(packFileUrl).then(res => res.text());
  const pages = [];
  const regionsData = [];
  const kerningsData = [];
  let pageImage = null;
  const lines = packFileContent.split('\n').map(s => s.trim());
  let i = 0;
  let tuple;
  let tmp_array = [];
  let push_to_kerning = false;
  let numOfPages = 0;
  let startPageLine = 2;

  while (true) {
    let line = lines[startPageLine];
    tmp_array.length = 0;
    tuple = line.split(' ').map(s => s.trim());

    if (tuple[0] === 'page') {
      numOfPages += 1;
    } else {
      break;
    }

    startPageLine += 1;
  }

  let pageCounter = -1;
  let saveIndex = 2 + numOfPages + 1; //starting line's index to find region data

  while (true) {
    let line = lines[i];

    if (line === undefined) {
      push_to_kerning = false;
      break;
    }

    if (line.split(' ').length === 2) {
      const tmp = line.split(' ');

      if (tmp[0] === 'kernings') {
        push_to_kerning = true;
      }

      i++;
      line = lines[i];
    }

    if (push_to_kerning && pageImage !== null) {
      tuple = line.split(' ').map(s => s.trim());
      tuple = tuple.filter(item => item !== '');
      tmp_array.length = 0;

      for (let i = 1; i < tuple.length; i++) {
        tmp_array.push(tuple[i].split('=')[1]);
      }

      const first = parseInt(tmp_array[0], 10);
      const second = parseInt(tmp_array[1], 10);
      const amount = parseInt(tmp_array[2], 10);
      kerningsData.push({
        first,
        second,
        amount
      });
    }

    if (pageImage === null) {
      pageCounter += 1;
      tmp_array.length = 0;
      tuple = line.split(' ').map(s => s.trim());

      for (let i = 1; i < tuple.length; i++) {
        tmp_array.push(tuple[i].split('=')[1]);
      }

      const face = tmp_array[0];
      const size = parseInt(tmp_array[1], 10);
      const bold = parseInt(tmp_array[2], 10) === 0 ? false : true;
      const italic = parseInt(tmp_array[3], 10) === 0 ? false : true;
      const charset = tmp_array[4];
      const unicode = parseInt(tmp_array[5], 10);
      const stretchH = parseInt(tmp_array[6], 10); // percentage

      const smooth = parseInt(tmp_array[7], 10);
      const aa = parseInt(tmp_array[8], 10);
      const padding = tmp_array[9].split(',').map(i => parseInt(i, 10));
      const spacing = tmp_array[10].split(',').map(i => parseInt(i, 10));
      const outline = parseInt(tmp_array[11], 10);
      i++;
      line = lines[i];
      tmp_array.length = 0;
      tuple = line.split(' ').map(s => s.trim());

      for (let i = 1; i < tuple.length; i++) {
        tmp_array.push(tuple[i].split('=')[1]);
      }

      const lineHeight = parseInt(tmp_array[0], 10);
      const base = parseInt(tmp_array[1], 10);
      const scaleW = parseInt(tmp_array[2], 10);
      const scaleH = parseInt(tmp_array[3], 10);
      const num_of_pages = parseInt(tmp_array[4], 10);
      const packed = parseInt(tmp_array[5], 10);
      const alphaChnl = parseInt(tmp_array[6], 10);
      const redChnl = parseInt(tmp_array[7], 10);
      const greenChnl = parseInt(tmp_array[8], 10);
      const blueChnl = parseInt(tmp_array[9], 10);
      i = i + pageCounter + 1;
      line = lines[i];
      tmp_array.length = 0;
      tuple = line.split(' ').map(s => s.trim());

      for (let i = 1; i < tuple.length; i++) {
        tmp_array.push(tuple[i].split('=')[1]);
      }

      const page_id = parseInt(tmp_array[0], 10);
      const url = tmp_array[1].split('"');
      const file = concatAndResolveUrl(packFileUrl, `../${url[1]}`);
      pageImage = {
        file,
        page_id,
        face,
        size,
        bold,
        italic,
        charset,
        unicode,
        stretchH,
        smooth,
        aa,
        padding,
        spacing,
        outline,
        lineHeight,
        base,
        scaleW,
        scaleH,
        num_of_pages,
        packed,
        alphaChnl,
        redChnl,
        greenChnl,
        blueChnl
      };
      pages.push(pageImage);
      i = saveIndex;
      line = lines[i];
    }

    if (!push_to_kerning && pageImage !== null) {
      tuple = line.split(' ').map(s => s.trim());
      tuple = tuple.filter(item => item !== '');
      tmp_array.length = 0;

      for (let i = 1; i < tuple.length; i++) {
        tmp_array.push(tuple[i].split('=')[1]);
      }

      const id = parseInt(tmp_array[0], 10);
      const left = parseInt(tmp_array[1], 10);
      const top = parseInt(tmp_array[2], 10);
      const width = parseInt(tmp_array[3], 10);
      const height = parseInt(tmp_array[4], 10);
      const xoffset = parseInt(tmp_array[5], 10);
      const yoffset = parseInt(tmp_array[6], 10);
      const xadvance = parseInt(tmp_array[7], 10);
      const page = parseInt(tmp_array[8], 10);
      const region = {
        id,
        left,
        top,
        width,
        height,
        xoffset,
        yoffset,
        xadvance,
        page: pageImage,
        id_page: page
      };

      if (region.id_page === pageCounter) {
        regionsData.push(region);
      } else {
        if (pageCounter === numOfPages - 1) {
          push_to_kerning = true;
        } else {
          saveIndex = i;
          i = -1;
          pageImage = null;
        }
      }
    }

    i++;
  }

  for (let page of pages) {
    page.texture = await loadTexture(gl, page.file, textureOptions);
    page.invTextWidth = 1 / page.texture.width;
    page.invTextHeight = 1 / page.texture.height;
  }

  const regions = [];

  for (let regionData of regionsData) {
    const left = regionData.left;
    const top = regionData.top;
    const width = regionData.width;
    const height = regionData.height;
    const {
      texture,
      invTexWidth,
      invTexHeight
    } = regionData.page;
    regions.push(new TextureRegion(texture, left, top, width, height, regionData, invTexWidth, invTexHeight));
  }

  return {
    getLineHeight() {
      return pageImage.lineHeight;
    },

    getPages() {
      return pages;
    },

    getRegions() {
      return regions;
    },

    createRenderer(displayWidth) {
      return new TextRenderer(pageImage, regions, kerningsData, displayWidth);
    }

  };
});